import React from 'react';
import {Route, Switch} from 'react-router-dom';
import {HomePage, CartPage} from '../pages';
import ShopHeader from '../shop-header';
import './app.css';

const App = () => {
    return (
        <main role='main' className='container'>
            <ShopHeader />
            <Switch>
                <Route
                path='/BookStore/'
                component={HomePage}
                exact
                />
                <Route
                path='/BookStore/cart'
                component={CartPage}
                />
            </Switch>
        </main>
    )
};

export default App;